<template>
  <div class="tabs">
    <span class="left">{{todoslength}} item.length</span>
    <span class="buttons">
      <span
        v-for="state in states"
        :key="state"
        :class="[state, filter === state ? 'set' : '']"
        @click="toggleFilter(state)"
      >
        {{state}}
      </span>
    </span>
    <span class="right" @click="clearall">clear all</span>
  </div>
</template>
<script>
export default {
  name: 'tabs',
  props: {
    filter: {
      type: String,
      required: true
    },
    todos: {
      type: Array,
      required: true
    }
  },
  data () {
    return {
      states: ['all', 'active', 'completed']
    }
  },
  computed: {
    todoslength () {
      return this.todos.filter(todo => !todo.completed).length
    }
  },
  methods: {
    toggleFilter (state) {
      this.$emit('taggle', state)
    },
    clearall () {
      this.$emit('clearall')
    }
  }
}
</script>

<style scoped></style>



