<template>
  <div :class="['todo-item', todo.completed ? 'completed' : '']" >
    <input
      type="checkbox"
      class="toggle"
      v-model="todo.completed"
    />
    <label>{{todo.content}}</label>
    <button class="destory" @click="deletetodo">删除</button>
  </div>
</template>

<script>
export default {
  name: 'item',
  props: {
    todo: {
      type: Object,
      required: true
    }
  },
  methods: {
    deletetodo () {
      this.$emit('del', this.todo.id)
    }
  }
}
</script>

<style scoped></style>



