<template>
  <div class="todu">
    <Header></Header>
    <input
      type="text"
      class="add-input"
      autofocus="autofocus"
      placeholder="接下去要做什么"
      @keyup.enter="addTodo"
    >
    <Items
      :todo="todo"
      v-for="todo in filteredtodos"
      :key="todo.id"
      @del="deleteTodo"
    />
    <Tabs
      :filter="filter"
      :todos="todos"
      @taggle="tagglefilter"
      @clearall="clearall"
    />
    <Footer></Footer>
  </div>
</template>
<script>
import Header from '@/components/header'
import Footer from '@/components/footer'
import Items from '@/components/items'
import Tabs from '@/components/tabs'
let id = 0
export default {
  data () {
    return {
      todos: [],
      filter: 'all'
    }
  },
  methods: {
    addTodo (e) {
      this.todos.unshift({
        id: id++,
        content: e.target.value.trim(),
        completed: false
      })
      e.target.value = ''
    },
    deleteTodo (id) {
      this.todos.splice(this.todos.findIndex(todo => todo.id === id), 1)
    },
    tagglefilter (state) {
      this.filter = state
    },
    clearall () {
      this.todos = this.todos.filter(todo => todo.completed === false)
    }
  },
  computed: {
    filteredtodos () {
      if (this.filter === 'all') {
        return this.todos
      }
      if (this.filter === 'active') {
        return this.todos.filter(todo => todo.completed === false)
      } else {
        return this.todos.filter(todo => todo.completed === true)
      }
    }
  },
  components: {
    Header,
    Footer,
    Items,
    Tabs
  }
}
</script>

<style scoped></style>



